body {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #eee;
}
.app {
  text-align: center;
  max-width: 400px;
  margin: 0 auto;
}
div.buttons button {
  font-size: 20px;
  padding: 5px 10px;
  margin: 5px;
  min-width: 80px;
  border: 1px solid black;
  background-color: #fff;
  -webkit-transition: .2s;
  transition: .2s;
  cursor: pointer;
  outline: none;
}

div.buttons button:hover {
  background-color: black;
  color: #fff;
}
h1.header {
  text-transform: uppercase;
  letter-spacing: 2px;
}
div.singelPersonDisplay {
  background-color: #fff;
  width: 95%;
  margin: 10px auto;
  border: 1px solid black;
}

div.singelPersonDisplay h3,
div.singelPersonDisplay h4,
div.singelPersonDisplay p {
  margin: 5px 0;
}

div.singelPersonDisplay p span {
  font-weight: bold;
}
